import React, { createRef, ReactNode } from 'react';
import { useIsVisible } from '../../../hooks/useIsVisible';
import building from '../../../media/building.svg';
import college from '../../../media/college.svg';
import graduation from '../../../media/graduation.svg';

interface ExperienceProps {
  from: string;
  to: string;
  experience: string;
  link: string;
  place: string;
  type: 'work' | 'graduation' | 'internship';
  last?: boolean;
}

export const Experience = ({ from, to, experience, link, place, type, last = false }: ExperienceProps) => {
  const ref = createRef<HTMLDivElement>();
  const isVisible = useIsVisible(ref);

  return (
    <div ref={ref} className={`experience${isVisible ? ' visible' : ''}`}>
      <div className="year">{from}<br />{to}</div>
      <div className="type-connector">
        <div className="type">
          {type === "work" && <img src={building} />}
          {type === "internship" && <img src={college} />}
          {type === "graduation" && <img src={graduation} />}
        </div>
        {!last && <div className="connector" />}
      </div>
      <div className="details">
        <div className="title">{experience} </div>
        <div className="location">
          <a href={link} target="_blank">
            <span>
              @&thinsp;</span>{place}</a>
        </div> <div className="year-resp"><span>{from}</span><span>&thinsp;-&thinsp;</span><span>{to}</span></div>
      </div>
    </div >
  );
}

interface ExperiencesProps {
  children: ReactNode;
}

export const Experiences = ({ children }: ExperiencesProps) => {
  return (
    <div className="experiences">
      {children}
    </div>
  );
}