import React, { createRef } from 'react';
import { Page } from '../../common/components/page';
import { WorkTitle } from '../../common/components/work-title';
import { useIsVisible } from '../../hooks/useIsVisible';

interface PageContactProps {
}

export const PageContact = ({ }: PageContactProps) => {
  const refButton = createRef<HTMLDivElement>();
  const isVisible = useIsVisible(refButton);

  return (
    <Page className="h-full">
      <div className="card">
        <WorkTitle>CONTACT ME</WorkTitle>
        <div ref={refButton} className={`contact-me${isVisible ? ' visible' : ''}`}>
          <a href="https://www.linkedin.com/in/federico-garcia-garcia/" target="_blank">
            LinkedIn
          </a>
        </div>
      </div>
    </Page>
  );
}