import React from 'react';
import { Page } from '../../common/components/page';
import { WorkTitle } from '../../common/components/work-title';
import { Experience, Experiences } from './components/experience';

interface PageExperienceProps {
}

export const PageExperience = ({ }: PageExperienceProps) => {
  return (
    <Page className="h-min">
      <div className="card">
        <WorkTitle>EXPERIENCE</WorkTitle>
        <Experiences>
          <Experience type="work" from="2022 Sep" to="2023 Mar" experience="Frontend Developer" place="TEKsystems at Apple" link="https://www.teksystems.com/" />
          <Experience type="work" from="2020 Sep" to="2022 Aug" experience="Frontend Developer" place="Skeleton Crew Studio" link="https://www.skeletoncrew.co.jp/" />
          <Experience type="internship" from="2018 Jan" to="2018 Dec" experience="Frontend Developer Internship" place="University of Granada" link="https://www.ugr.es/en" />
          <Experience last type="graduation" from="2015 Sep" to="2019 Nov" experience="Bachelor in Computer Science" place="University of Granada" link="https://www.ugr.es/en" />
        </Experiences>
      </div>
    </Page>
  );
}

