import React, { createRef, ReactNode } from 'react';
import { useIsVisible } from '../../../hooks/useIsVisible';

interface SkillProps {
  children: ReactNode;
}

export const Skill = ({ children }: SkillProps) => {
  const ref = createRef<HTMLDivElement>();
  const isVisible = useIsVisible(ref);

  return (
    <div ref={ref} className={`skill-container${isVisible ? ' visible' : ''}`}>
    <div className="skill">
        {children}
      </div>
    </div>
  );
}

interface SkillsProps {
  children: ReactNode;
}

export const Skills = ({ children }: SkillsProps) => {
  return (
    <div className="skills">
      {children}
    </div>
  );
}

interface SkillsColumnProps {
  children: ReactNode;
}

export const SkillsColumn = ({ children }: SkillsColumnProps) => {
  return (
    <div className="skills-column">
      {children}
    </div>
  );
}