import { Canvas, useFrame, useLoader, useThree  } from '@react-three/fiber';
import React, { useRef, useEffect, useMemo, useState } from 'react';
import backgroundAnimated from './background-animated.svg';
import background from './background.svg';
import backgroundTexture from './background.png';
import { isMobile, isFirefox } from 'react-device-detect'
import { color2RGB, hslToRgb } from '../../utils/utils';
import { TextureLoader } from 'three/src/loaders/TextureLoader';
import { Mesh, Texture } from 'three';

interface BackgroundGradientProps {
}

export const BackgroundGradient = ({ }: BackgroundGradientProps) => {
  const backRef = useRef<HTMLDivElement>(null!);
  const [backgroundGradient, setBackgroundGradient] = useState("");
  const [show, setShow] = useState(true);
  const colorMap = useLoader(TextureLoader, backgroundTexture);

  useEffect(() => {

    if(backRef.current) {
      const appScroll = document.getElementById('App-scroll');
      if(appScroll) {
        const hue = appScroll.scrollTop / appScroll.scrollHeight;

        setBackgroundGradient(`linear-gradient(0,
          ${color2RGB(hslToRgb((hue + 34/360) % 1.0, 1, 66/100))},
          ${color2RGB(hslToRgb((hue + 356/360) % 1.0, 1, 56/100))},
          ${color2RGB(hslToRgb((hue + 284/360) % 1.0, 1, 46/100))})`
        );
      }
    }
  }, [backRef.current]);

  return (
    <>
      <div
        style={{
          position: "fixed",
          left: 0,
          top: 0,
          width: "100%",
          height: "100%",
        }}
      >
        <div
          ref={backRef}
          className="background-gradient"
          style={{background: backgroundGradient}}
        >
        </div>
        <Canvas
          orthographic
          camera={{
            zoom: 100,
            position: [0, 0, 100]
          }}
        >
          <MeshBackground onChange={(flag) => setShow(flag)} />
          <MeshWavy
            colorMap={colorMap as Texture}
            x={-1/2.8}
            y={-1/2}
            scale={1.2}
            speed={-1/8}
          />
          <MeshWavy
            colorMap={colorMap as Texture}
            x={-1/3}
            y={-1/2}
            rotation={30}
            speed={-1/5}
          />
          <MeshWavy
            colorMap={colorMap as Texture}
            x={1/2.8}
            y={1/2}
            scale={1.2}
            speed={-1/8}
          />
          <MeshWavy
            colorMap={colorMap as Texture}
            x={1/3}
            y={1/2}
            rotation={30}
            speed={-1/5}
          />
        </Canvas>
      </div>
    </>
  );
}

interface MeshBackgroundProps {
  onChange: (flag: boolean) => void;
}

const MeshBackground = ({ onChange }: MeshBackgroundProps) => {
  const fragmentShader =
    `
    varying vec2 vUv;
    uniform float uTime;
    uniform float uHue;

    float hue2rgb(float p, float q, float t) {
      if (t < 0.0) t += 1.0;
      if (t > 1.0) t -= 1.0;

      if (t < 1.0 / 6.0) return p + (q - p) * 6.0 * t;
      if (t < 1.0 / 2.0) return q;
      if (t < 2.0 / 3.0) return p + (q - p) * (2.0 / 3.0 - t) * 6.0;
    
      return p;
    }

    vec3 hslToRgb(float h, float s, float l) {
      float r, g, b;

      if (s == 0.0) {
        r = l;
        g = l;
        b = l;
      } else {
        float q = l < 0.5 ? l * (1.0 + s) : l + s - l * s;
        float p = 2.0 * l - q;
        r = hue2rgb(p, q, h + 1.0 / 3.0);
        g = hue2rgb(p, q, h);
        b = hue2rgb(p, q, h - 1.0 / 3.0);
      }

      return vec3(r, g, b);
    }

    void main() {
      float x = vUv.x / 20.0;
      vec3 color1 = hslToRgb(mod(uHue + x + uTime + 284.0 / 360.0, 1.0), 0.9, 56.0 / 100.0);
      vec3 color2 = hslToRgb(mod(uHue + x + uTime + 356.0 / 360.0, 1.0), 0.9, 56.0 / 100.0);
      vec3 color3 = hslToRgb(mod(uHue + x + uTime + 34.0 / 360.0, 1.0), 0.9, 56.0 / 100.0);

      float a = 0.0;
      float b = 0.0;
      float c = 0.0;

      if(vUv.y > 0.5) {
        float y = (vUv.y - 0.5) * 2.0;
        a = y;
        b = 1.0 - a;
      }
      else {
        float y = vUv.y * 2.0;
        b = y;
        c = 1.0 - b;
      }

      gl_FragColor = vec4((color1 * a + color2 * b + color3 * c), 1.0);
    }
  `;

  const vertexShader =
    `
    varying vec2 vUv;
    void main() {
      vec4 modelPosition = modelMatrix * vec4(position, 1.0);
      vec4 viewPosition = viewMatrix * modelPosition;
      vec4 projectedPosition = projectionMatrix * viewPosition;
    
      gl_Position = projectedPosition;
      vUv = uv;
    }
  `;

  const matRef = useRef<THREE.ShaderMaterial>(null!);
  const { viewport } = useThree();

  const uniforms = useMemo(() => ({
    uTime: { value: 0.0 },
    uHue: { value: 0.0 },
  }), []);

  useFrame(({ clock }) => {
    if (matRef.current) {
      const appScroll = document.getElementsByClassName("AppScroll")[0];

      if (appScroll) {
        let newHue = 0;
        
        if(appScroll.scrollTop > window.innerHeight*3.5) {
          onChange(false);
          newHue = -0.35;
        }
        else if(appScroll.scrollTop > window.innerHeight/3) {
          onChange(false);
          newHue = -0.2;
        }
        else {
          onChange(true);
        }

        matRef.current.uniforms.uHue.value = matRef.current.uniforms.uHue.value * 0.985 + newHue * 0.015;
      }

      //matRef.current.uniforms.uTime = { value: Math.sin(clock.getElapsedTime())/20 }
    }
  });

  useEffect(() => {
    if (matRef.current) {
      const appScroll = document.getElementsByClassName('App')[0];

      if (appScroll) {
        const newHue = appScroll.scrollTop / appScroll.scrollHeight;
        matRef.current.uniforms.uHue.value = newHue;
      }
    }
  }, [matRef.current]);

  return (
    <mesh>
      <planeBufferGeometry args={[viewport.width, viewport.height]} />
      <shaderMaterial
        ref={matRef}
        fragmentShader={fragmentShader}
        vertexShader={vertexShader}
        uniforms={uniforms}
      />
    </mesh>
  );
}

interface MeshWavyProps {
  colorMap: Texture;
  x: number;
  y: number;
  scale?: number;
  rotation?: number;
  speed?: number;
}

const MeshWavy = ({ colorMap, x, y, scale=1, rotation=0, speed=1 }: MeshWavyProps) => {
  const meshRef = useRef<Mesh>(null!);
  const matRef = useRef<THREE.MeshBasicMaterial>(null!);
  const { viewport } = useThree();

  useFrame(({ clock }) => {
    const appScroll = document.getElementsByClassName("AppScroll")[0];

    if (meshRef.current) {
      meshRef.current.rotation.z = clock.getElapsedTime() * speed + rotation;

      // if (appScroll) {
      //   let newScale = 1;
        
      //   if(appScroll.scrollTop > window.innerHeight*3.5) {
      //     newScale = 1;
      //   }
      //   else if(appScroll.scrollTop > window.innerHeight/3) {
      //     newScale = 0.89;
      //   }

      //   newScale *= Math.min(viewport.width, viewport.height);

      //   let scale = meshRef.current.scale.x * 0.96 + newScale * 0.04;
      //   meshRef.current.scale.set(scale, scale, scale);
      // }
    }
    
    if (matRef.current) {

      if (appScroll) {
        let newOpacity = 0;
        
        if(appScroll.scrollTop > window.innerHeight*3.5) {
          newOpacity = 0.075;
          
        }
        else if(appScroll.scrollTop > window.innerHeight/3) {
          newOpacity = 0.05;
        }
        else {
          newOpacity = 0.075;
        }

        matRef.current.opacity = matRef.current.opacity * 0.95 + newOpacity * 0.05;
      }
    }
  });

  return (
    <mesh
      ref={meshRef}
      scale={[
        Math.min(viewport.width, viewport.height),
        Math.min(viewport.width, viewport.height),
        1
      ]}
      position={[
        viewport.width * x,
        viewport.height * y,
        0
      ]}
      >
      <planeGeometry args={[scale, scale]} />
      <meshBasicMaterial ref={matRef} map={colorMap} opacity={0.075} transparent />
    </mesh>
  );
}
