import React, { CSSProperties, ReactNode } from 'react';

interface ProjectProps {
  children: ReactNode
  page: number;
  current: number;
  title?: string;
  stack?: string[];
  descriptionStyle?: CSSProperties;
  onClickBack: () => void;
}

export const Project = ({ children, page, current, title, stack, descriptionStyle, onClickBack }: ProjectProps) => {
  return (
    <div
      className={`project ${page === current ? "active" : ""}`}
    >
      {title && <h3>{title}</h3>}
      <div className="project-description" style={descriptionStyle}>{children}</div>
      <div className="stack">
        {stack && stack.map(tool => <span className="tool" key={tool}>
          {tool}{' '}
        </span>)}</div>
        <button onClick={() => onClickBack()} disabled={page !== current}>Back</button>
    </div >
  );
}
