import React, { ReactNode } from 'react';
import { useState, createRef } from 'react';
import { Page } from '../../common/components/page';
import { WorkTitle } from '../../common/components/work-title';
import { useIsVisible } from '../../hooks/useIsVisible';
import { Project } from "./components/project";

interface PageProjects {
}

export const PageProjects = ({ }: PageProjects) => {
  const W = 60;
  const NUM_PROJECTS = 5;
  const [page, setPage] = useState(0);

  return (
    <Page className='h-min'>
      <div className="card">
        <WorkTitle>PROJECTS</WorkTitle>
        <div className="projects">
          <ul className={`projects-list${page === 0 ? ' active' : ''}`}>
            <ProjectTitle onClick={() => {setPage(1)}}>AI/ML data UI</ProjectTitle>
            <ProjectTitle onClick={() => {setPage(2)}}>Interactive Audio Guide</ProjectTitle>
            <ProjectTitle onClick={() => {setPage(3)}}>Company Virtual Expo</ProjectTitle>
            <ProjectTitle onClick={() => {setPage(4)}}>Virtual Event Framework</ProjectTitle>
            <ProjectTitle onClick={() => {setPage(5)}}>Employee Management System</ProjectTitle>
          </ul>
          <Project onClickBack={() => setPage(0)} page={1} current={page} title="AI/ML data UI" stack={["React", "HTML", "CSS", "Jest", "Zod", "TypeScript", "JavaScript"]}>
            <p>I worked in the front-end of a web application that helps AI/ML engineers visualize and understand high volumes of data as well as manage various aspects of their workflow.</p>
            <p>By visualizing and comparing different metrics, users can make informed decisions.</p>
          </Project>
          <Project onClickBack={() => setPage(0)} page={2} current={page} title="Interactive Audio Guide" stack={["React", "HTML", "CSS", "JavaScript", "ASP.NET"]}>
            <p>I worked together with a design company and museum building a visual novel style audio guide app used by thousands of users.</p>
            <p>Users can see and hear characters explain the history of Kyoto, play quizzes, and unlock new content by visiting designated locations using GPS.</p>
          </Project>
          <Project onClickBack={() => setPage(0)} page={3} current={page} title="Company Virtual Expo" stack={["HTML", "CSS", "JavaScript", "jQuery", "Bootstrap", "ASP.NET", "SignalR"]}>
            <p>I was in charge of the front-end of the website for a project requested by the City of Kyoto, where local companies can register and create online booths to showcase their products to appeal to a wide range of customers online.</p>
            <p>Prospect customers can contact them through a custom implemented messaging system for the purpose of expanding business opportunities.</p>
          </Project>
          <Project onClickBack={() => setPage(0)} page={4} current={page} title="Virtual Event Framework" stack={["HTML", "CSS", "JavaScript", "React.js", "Unity", "Unreal"]}>
            <p>I migrated an in-house framework for developing 3D online virtual tours to React.js and implemented performance improvements.</p>
            <p>Other projects began being developed using the new framework.</p>
          </Project>
          <Project onClickBack={() => setPage(0)} page={5} current={page} title="Employee Management System" stack={["HTML", "CSS", "JavaScript", "jQuery" ,"ASP.NET"]}>
            <p>I expanded the functionality, improved the UI, and fixed several bugs for the employee management system of a Japanese multinational that manufactures construction equipment.</p>
          </Project>
        </div>
      </div>
    </Page>
  );
}


interface ProjectTitleProps {
  children: ReactNode;
  onClick: () => void
}

export const ProjectTitle = ({ children, onClick }: ProjectTitleProps) => {
  const ref = createRef<HTMLButtonElement>();
  const isVisible = useIsVisible(ref);

  return (
    <li className={`project-title${isVisible ? ' visible' : ''}`}>
      <button onClick={() => onClick()} ref={ref}>
        {children}
      </button>
    </li>
  );
}